.containerGeneral {
    flex: 1 1;
    height: 75px;
    display: flexbox;
    display: -webkit-flex;
    flex-direction: row;
    background-color: #000;
}

.element {
    color: white;
}

.list {
    flex: 1 1;
    display: flexbox;
    display: -webkit-flex;
    flex-direction: row;
}

.listElement {
    flex: 1 1;
    padding: 5px;
    font-size: 30px;
    list-style-type: none;
    text-decoration: none;
    text-transform: uppercase;
    color: #f0d03b;
}

.menu {
    align-items: center;
}

#body {
    background-color: #f00;
    flex: 1 1;
    flex-direction: column;
    align-content: center;
    align-items: center;
    justify-content: center;
    justify-self: center
}

#page {
    display: flex;
    flex-direction: column;
    flex: 1 1;
}

.clickable {
    cursor: pointer;
}

.notClickable {
    cursor: default;
}

.unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.alignSelf {
    align-self: center;
    flex-direction: column;
    align-content: center;
    align-items: center;
    justify-content: center;
    justify-self: center
}

.loginContainer {
    position: fixed;
    top: -160px;
    right: 0px;
    height: 220px;
    width: 200px;
    border-bottom-left-radius: 10px;
    color: white;
    background-color: #fff;
    display: flexbox;
    padding: 20px;
    background-color: #0008;
    border-bottom: 3px double #f0d03b;
    border-left: 3px double #f0d03b;

    transition: top .3s ease;
    -moz-transition: top .3s ease;
    -o-transition: top .3s ease;
    -webkit-transition: top .3s ease;
}

.active {
    top: 0px;
}
.container {
    display: flex;
    overflow: hidden;
    border-radius: 3px;
    padding: 0px 10px 0px;
    margin: 5px;
    box-shadow: 2px 5px 10px #000a;
    flex: 1 1;
    height: 75px;
    width: 100%;
    flex-wrap: row wrap;
    transition: height .3s ease;
    -moz-transition: height .3s ease;
    -o-transition: height .3s ease;
    -webkit-transition: height .3s ease;
}

.containerOnClick {
    height: 118px;
}

.calendarContainer {
     flex: 1 1;
     overflow: hidden;
     background-color: #fff;
     height: 400px;
     margin: auto;
}

.meetingTitle {
    padding: 0px;
    margin: 0px;
    margin-left: 10px;
}

.eventData {
    flex: 1 1;
}

.description {
    flex-wrap: inherit wrap;
    flex: .7 1;
    padding-top: 100px;
    transition: padding-top .3s ease-in;
    -moz-transition: padding-top .3s ease-in;
    -o-transition: padding-top .3s ease-in;
    -webkit-transition: padding-top .3s ease-in;
}

.containerOnClick .description {
    padding-top: 2px;
    transition: padding-top .3s ease-in;
    -moz-transition: padding-top .3s ease-in;
    -o-transition: padding-top .3s ease-in;
    -webkit-transition: padding-top .3s ease-in;
}

.date {
    flex: .20 1;
    padding-top: 2px;
    flex-wrap: column nowrap;
}

.info {
    display: flex;
    flex: 1 1;
    padding-top: 7px;
    flex-wrap: row nowrap;
}

div {
    flex: 1 1;
}

h1 {
    margin: 0px;
    padding: 0px;
}

h2 {
    margin: 0px;
    font-size: 40px;
}

h3 {

    border: 0px;
    font-size: 20px;
}

p {
    padding: 0px;
    margin: 0px;
    flex: 1 1;
}
.fieldContainer {
    margin-top: 5px;
    flex: 1 1;
    display: -webkit-flex;
    flex-direction: row;
}

.inputContainer {
    flex: 1 1;
}

.general {
    max-width: 150px;
    padding: 8px;
    margin-top: 5px;
    background: #f4f7f8;
    justify-self: center;
    border-radius: 8px;
    font-family: Georgia, "Times New Roman", Times, serif;
}

.general fieldset {
    border: none;
}

.general legend {
    font-size: 1.4em;
    margin-bottom: 10px;
}

.general label {
    display: block;
    margin-bottom: 8px;
}

.general input[type="text"],
.general input[type="date"],
.general input[type="datetime"],
.general input[type="email"],
.general input[type="number"],
.general input[type="search"],
.general input[type="time"],
.general input[type="url"],
.general textarea,
.general select {
    font-family: Georgia, "Times New Roman", Times, serif;
    background: rgba(255, 255, 255, .1);
    border: none;
    border-radius: 4px;
    font-size: 12px;
    margin: 0;
    outline: 0;
    padding: 1px;
    width: 80%;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    background-color: #e8eeef;
    color: #8a97a0;
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.03) inset;
    margin-bottom: 3px;
}

.general input[type="text"]:focus,
.general input[type="date"]:focus,
.general input[type="datetime"]:focus,
.general input[type="email"]:focus,
.general input[type="number"]:focus,
.general input[type="search"]:focus,
.general input[type="time"]:focus,
.general input[type="url"]:focus,
.general textarea:focus,
.general select:focus {
    background: #d2d9dd;
}

.general select {
    -webkit-appearance: menulist-button;
    height: 35px;
}

.general .number {
    background: #1abc9c;
    color: #fff;
    height: 30px;
    width: 30px;
    display: inline-block;
    font-size: 0.8em;
    margin-right: 4px;
    line-height: 30px;
    text-align: center;
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.2);
    border-radius: 15px 15px 15px 0px;
}

.general input[type="submit"],
.general input[type="button"] {
    position: relative;
    display: block;
    color: #FFF;
    margin: 0 auto;
    background: #1abc9c;
    font-size: 18px;
    text-align: center;
    font-style: normal;
    width: 100%;
    border: 1px solid #16a085;
    border-width: 1px 1px 3px;
    margin-bottom: 10px;
}

.general input[type="submit"]:hover,
.general input[type="button"]:hover {
    background: #109177;
}


.day {
	font-weight: bold;
}


table#officeHours {
	width: 100%;
	margin: auto;
}

